import React from 'react'
import PropTypes from 'prop-types'

// Import typefaces
import 'typeface-montserrat'
import 'typeface-merriweather'

import profilePic from './avatar_wings.jpg'

class Bio extends React.Component {
  render() {
    const { name, slogan, textOnly } = this.props;
    return (
      <p>
        {
          textOnly ? null : (
            <img
              src={profilePic}
              alt={`Kyle Mathews`}
              style={{
                float: 'left',
                marginBottom: 0,
                borderRadius: '40px',
              }}
            />
          )
        }
        <strong>{name}</strong> 科技世界的快乐旅行者，始终相信 “{slogan}”
      </p>
    )
  }
}

Bio.propTypes = {
  name: PropTypes.string,
  solgan: PropTypes.string,
  textOnly: PropTypes.bool,
}
Bio.defaultProps = {
  name: 'Snow Grus',
  slogan: '没有最好，只有更好',
  textOnly: true,
}

export default Bio



