import React from 'react'
import get from 'lodash/get'
import assign from 'lodash/assign'
import Link from 'gatsby-link'

function Footer({ title, style }) {
  return (
    <div className="rw-footer" style={style}>
      <Link
        to={'/'}
      >
        {title}
      </Link>
    </div>
  )
}

export default Footer



